import React, { useState, useEffect } from 'react';
import Header from './components/Header';
import CinemaSelector from './components/CinemaSelector';
import MovieSelection from './components/MovieSelection';
import DateTimeSelector from './components/DateTimeSelector';
import axios from 'axios';
import { useCookies } from 'react-cookie';
import config from './constant/config';
import './App.css';

function uuidv4() {
  return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
    (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
  );
}

function App(props) {
  const [cinema, setCinema] = useState({});
  const [dateTime, setDateTime] = useState({});
  const [views, setViews] = useState(0);

  const [cookies, setCookie] = useCookies(['id']);
  if (!cookies.id) {
    const uuid = uuidv4();
    setCookie('id', uuid, { path: '/' });
  }

  axios.get(`${config.apiUrl}views`, { withCredentials: true })
    .then((r) => setViews(r.data));

  return (
    <div>
      <Header />
      <div className="content">
        <span className="description">
          {config.description}
          <br />
          <br />
          <b>{views}</b> <i>unique visitors</i>
        </span>
        <CinemaSelector selectCinema={(num, name) => setCinema({ num, name })}/>
        <DateTimeSelector selectDateTime={(date, time) => setDateTime({ date, time })}/>
        <MovieSelection cinema={cinema} dateTime={dateTime} />
        <span className="views"></span>
      </div>
    </div>
  );
}

export default App;
