import { useState } from 'react';
import moment from 'moment';
import './DateTimeSelector.css';

const today = moment();
const sixDays = moment().add(6, 'days');

const DateTimeSelector = (props) => {
  const [date, setDate] = useState('');
  const [time, setTime] = useState('');
  const [error, setError] = useState('');

  const onTimeChange = (e) => {
    const hourMinute = e.target.value.split(':');
    if (parseInt(hourMinute[0]) < 9) {
      setError('Only times between 09:00 and 23:59');
    } else {
      setTime(e.target.value);
      setDateTime(e.target.value, null);
      setError('');
    }
  };

  const onDateChange = (e) => {
    const datePicked = moment(e.target.value);
    if (datePicked <  moment().subtract(1, 'days') || datePicked > sixDays) {
      setError(`Only dates between ${today.format('DD/MM/YYYY')} and ${sixDays.format('DD/MM/YYYY')}`);
    } else {
      setDate(e.target.value);
      setDateTime(null, e.target.value);
      setError('');
    }
  };

  const { selectDateTime } = props;

  const setDateTime = (t = null, d = null) => {
    selectDateTime(d ? d : date, t ? t : time);
  }

  return (
    <div className="datetime-selection-container">
      <div className="datetime-selection">
        <input
          type="date"
          value={date}
          min={today.format('YYYY-MM-DD')}
          max={sixDays.format('YYYY-MM-DD')}
          onChange={(e) => onDateChange(e)}
        />
        <input
          type="time"
          value={time}
          min="09:00"
          max="23:59"
          onChange={(e) => onTimeChange(e)}
        />
      </div>
      { error !== '' &&
        <span className="error">{error}</span>
      }
    </div>
  );
};

export default DateTimeSelector;
