import React, { Component } from 'react';
import axios from 'axios';
import config from '../constant/config';
import './CinemaSelector.css';

class CinemaSelector extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cinemas: [],
    };
  }

  componentDidMount() {
    this.getCinemas();
  }

  async getCinemas() {
    // need to replace url when ready for production to a environemnt set one
    const resp = await axios.get(`${config.apiUrl}watchathon/cinemas`);
    if (resp.status === 200) {
      this.setState({ cinemas: resp.data });
    }
  }

  handleChange(e) {
    const { selectCinema } = this.props;

    const select = e.target;
    const cinemaNumber = select.value;
    const cinemaName = select.options[select.selectedIndex].text;

    selectCinema(cinemaNumber, cinemaName);
  }

  renderOptions() {
    const { cinemas } = this.state;
    return cinemas.map((cinema, i) => (
      <option key={i} value={cinema.number}>{cinema.name}</option>
    ));
  }

  render() {
    return (
      <div className="cinema-selector">
        <span>Select Cinema Location</span>
        <select
          className="selector"
          name="cinemas"
          defaultValue=""
          onChange={(e) => this.handleChange(e)}
        >
          <option key="-1" value="" disabled>Select Location</option>
          {this.renderOptions()}
        </select>
      </div>
    );
  }
}

export default CinemaSelector;
