import React, { Component } from 'react';
import axios from 'axios';
import moment from 'moment';
import config from '../constant/config';
import './MovieSelection.css';

class MovieSelection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      movies: [],
      selectedMovies: [],
      filteredMovies: [],
      selectedTypes: [],
      types: [],
    };
  }

  async getMovies() {
    const { cinema, dateTime } = this.props;
    // need to replace url when ready for production to a environemnt set one
    const resp = await axios.get(`${config.apiUrl}watchathon/movies`, {
      params: {
        cinema: cinema.num,
        date: dateTime.date,
        time: dateTime.time,
      }
    });
    if (resp.status === 200) {
      let types = [];
      for (let i = 0; i < resp.data.length; i += 1) {
        types = [...types, ...resp.data[i].times.map((time) => time.type)];
      }
      types = Array.from(new Set(types));
      this.setState({
        selectedTypes: types.slice(),
        types: types.slice(),
        movies: resp.data.slice(),
        filteredMovies: resp.data.slice(),
        selectedMovies: [],
      });
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { dateTime, cinema } = this.props;
    if (this.props !== prevProps && dateTime.time && dateTime.date && cinema.num && cinema.name) {
      this.getMovies();
    }
  }

  selectMovie(i) {
    const { selectedMovies } = this.state;
    if (selectedMovies.includes(i)) {
      const index = selectedMovies.indexOf(i);
      selectedMovies.splice(index, 1);
    } else {
      selectedMovies.push(i);
    }
    this.setState({ selectedMovies });
  }

  selectType(e, t) {
    const { types, selectedTypes, movies } = this.state;
    const index = selectedTypes.indexOf(t);
    if (index !== -1) {
      selectedTypes.splice(index, 1);
    } else {
      selectedTypes.push(t);
    }
    const filteredMovies = [];
    for (let i = 0; i < movies.length; i += 1) {
      const times = movies[i].times.filter((time) => selectedTypes.includes(time.type));
      if (times.length > 0) {
        const movieCopy = JSON.parse(JSON.stringify(movies[i]));
        movieCopy.times = times;
        filteredMovies.push(movieCopy);
      }
    }
    this.setState({
      selectedTypes,
      selectedMovies: [],
      filteredMovies,
    });
  }

  renderTypes() {
    const { selectedTypes, types } = this.state;
    return types.map((t, i) => (
      <div key={i}>
        <input
          className="type-input"
          type="checkbox"
          name={t}
          id={t}
          checked={selectedTypes.includes(t)}
          onChange={(e) => this.selectType(e, t)}
        />
        <label htmlFor={t}>{t}</label>
      </div>
    ));
  }

  renderMovies() {
    const { filteredMovies, selectedMovies } = this.state;
    return filteredMovies.map((m, i) => (
      <div key={i} className="movie-tile" onClick={() => this.selectMovie(i)}>
        {selectedMovies.includes(i) && <div className="selected"></div>}
        <img src={m.poster} />
        <span>{m.name}</span>
        <span className="small">{m.length} mins</span>
      </div>
    ));
  }

  renderOrder() {
    const { selectedMovies, filteredMovies } = this.state;
    const selectedMoviesInfo = [];
    for (let i = 0; i < selectedMovies.length; i += 1) {
        selectedMoviesInfo.push(filteredMovies[selectedMovies[i]]);
    }

    return selectedMoviesInfo.map((m, i) => (
      <div key={i} className="ordering-tile">
        <img src={m.poster} />
        <span>{m.name}</span>
        <span className="small">{m.length} mins</span>
        { m.times.map((t) => {
          console.log(t.time)
          const end = moment(t.time, 'HH:mm').add(m.length + 15, 'm');
          return (
          <div key={`${t.time}${t.type}`} className="time-type">
            <a
              href={t.link}
              target="__blank"
            >
              {t.time}
            </a>
            <span className="ends">
              ~{end.format('HH:mm')}
            </span>
            <span className="type">{t.type}</span>
          </div>
        );})
      }
      </div>
    ));
  }

  render() {
    const { dateTime, cinema } = this.props;
    const { selectedMovies } = this.state;
    if (dateTime.time && dateTime.date && cinema.num && cinema.name) {
      return (
        <div>
          <div className="movie-selection">
            <span>Choose Movies</span>
            <div className="types">
              {this.renderTypes()}
            </div>
            <div className="movies">
              {this.renderMovies()}
            </div>
          </div>
          { selectedMovies.length > 0 &&
            <div className="orderings">
              <span>Selected Movies Times'</span>
              <div className="start-end">
                <span className="start">start</span>
                { ' ' }
                <span className="end">end</span>
              </div>
              <div className="viewing-order">
                {this.renderOrder()}
              </div>
            </div>
          }
        </div>
      );
    }
    return <span />;
  }
}

export default MovieSelection;
